
@import "../../scss/main.scss";

.login {
	@include flexbox();
	position: relative;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	height: 100%;
	margin-left: 15px;
	animation: showIn 0.2s ease;
	@media (max-width: $md3+px) {
		margin: 0px;
	}
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right:0;
		bottom: 0;
		z-index: 0;
		background: rgba(0, 0, 0, 0.1);
	}
	&__background {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		z-index: 0;
	}
	&__container {
		font-size: 16px;
		background: rgba($accent, 1);
		width: 350px;
		min-height: 200px;
		border-radius: 8px;
		color: #fff;
		padding: 16px;
		z-index: 1;
		overflow: auto;
		@media (max-width: $md4+px) {
			border-radius: 0px;
			width: 100%;
		}
	}
	&__title {
		text-align: center;
		text-transform: uppercase;
		font-weight: 600;
		letter-spacing: 1px;
		margin-bottom: 10px;
	}
	&__quate {
		text-align: right;
		font-size: 13px;
	}
}


@keyframes showIn {
  from {  opacity: 0; }
  to   {  opacity: 1; }
}